<template>
    <div>
    <div class="main-home">
       <button @click="refreshPage">Refresh</button>
    </div>
    <div class="my-accounts">
        <h3>My Accounts</h3>
        <div class="account-list">
            <div v-if="this.currentUser?.linkedAccounts.length === 0">No accounts</div>
            <div v-for="account in this.currentUser.linkedAccounts" :key="account.accountId">
                
                    <details class="account-details">
                        <summary><b>Account Type</b>: {{ account.accountType }} </summary>
                        <p>Limitations: </p>
                        <div v-for="key in Object.keys(account.limitations)" :key="key">
                            {{ key }} : {{ account.limitations[key] }}
                        </div>
                        <router-link :to="{ name: 'account', params: { id: userId } }"><p> Go to account </p>
                        </router-link>
                        <button @click="makePayment(account)">Make Payment</button>
                        <payeePicker :user="this.currentUser" @selection-changed="payeeSelected"/>
                    </details> 
                        
                    
      
                </div>
           
        </div> 
        <button  @click="addAccounts">Add Account</button>
        <AddAccount v-if="isSetAccountDetails" :user="this.currentUser" @refreshUser="refreshUser"></AddAccount>
    </div>
    <div class="my-latest-trx" >
        <h3>My Latest Transactions</h3>
        <div class="transaction-list">
            <div v-if="this.currentUser?.recentTransactions.length === 0">No recent transactions</div>
            <div v-for="trx in this.currentUser.recentTransactions" :key="trx._id">
                <details class="transaction-details">
                    <summary>{{ trx.type === 'outgoing' ? '📤' : '📥' }} |<b>Amount</b> : {{ trx.amount  }}$ | <b>@</b> {{ new Date(trx.date)  }}</summary>
                    <p> Trx Id :  <router-link :to="{ name: 'transaction', params: { id: trx._id }, query : {trxId : trx._id }}">
                    {{ trx._id }}
                </router-link></p>
                    <p>Type : {{ trx.type }}</p>
                    <p>{{ trx.type === 'outgoing' ? `To: ${trx.referenceData.receiver.name }` : `From: ${trx.referenceData.sender.name }` }}  </p>
                    <p>Amount: {{ trx.amount }}</p>
                    <p>Status: {{ trx.status }}</p>
                </details>
                
               
            </div>
            
        </div> 
    </div>
</div>
</template>
<script>
import axios from 'axios';
import AddAccount from './addAccount.vue';
import payeePicker from './payeePicker.vue';

export default {
    name: 'MainHome',
    props: ['userId'],
    components: {
        AddAccount,
        payeePicker
    },
    emits: ['setUser'],
     mounted() {
       //console.log('UserId:', this.$route.query.userId);
         this.refreshPage();
    },
    data() {
        return {
           isSetAccountDetails: false,
           currentUser: {_id : this.userId,
              linkedAccounts: [],
              recentTransactions: []},
              selectedPayee: {}
        }
    },
    methods: {
        addAccounts() {
            this.isSetAccountDetails = true;
        },
        payeeSelected(payee) {
            this.selectedPayee = payee;
            console.log('payee selected', this.selectedPayee);  
        },
        refreshUser(user) {
            this.currentUser = user;
            this.isSetAccountDetails = false;
        },
        async refreshPage() {
            //fetch user
            console.log('refreshing page- ', this.userId);
            const response = await axios.get(
                `http://localhost:3030/api/user/${this.userId}`
            );
            console.log('user fetched successfully:', response.data);
            this.currentUser = response.data;
        },

        async makePayment(account) {
            if (!this.selectedPayee.userId) {
                alert('Please select a payee');
                return;
            }
            const amount = +prompt(`Please enter amount to transfer to ${this.selectedPayee.username}`);
            if (!amount || amount < 0) {
                alert('Please enter a valid amount');
                return;
            }

           

            const trx = {
                "accountId": account.accountId, // Reference to the Account Document of the transaction 
                "amount": amount,
                "type": "credit", // e.g., 'debit', 'credit'
                "details": {
                    "description": "Payment for services",
                    "userId": this.currentUser._id
                },
                "referenceData": {
                    "receiver": {
                        "userId" : this.selectedPayee.userId,
                        "accountId" : this.selectedPayee.accountId
                    }
                }
            };
            
            const repsonse = await  axios.post(
                `http://localhost:3030/api/transaction/${this.currentUser._id}`,
                trx
            );
            console.log('trx added successfully:', repsonse.data);
            alert ('Transaction added successfully id : ', repsonse.data._id);
            await this.refreshPage();
        }
    }
}
</script>
