<template>

    <div class="user-details">
        <h2>User Details</h2>
        <div class="user-details__header">
            <div> Username: {{ user.username }}</div>
            <div>Email : {{ user.email }}</div>
        </div>
        <div class="manage-button">
            <button @click="logoutUser">Logout</button>
            <button @click="deleteUser">Delete</button>
            <button @click="back">Back</button>
        </div>
        

    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'MainUser',
    mounted() {
        console.log('mounted user' ,this.user);
    },
    data() {
        return {
            user:{
                _id : this.$route.query.userId,
                username : this.$route.query.username,
                email : this.$route.query.email
            }
        }
    },
    methods: {
        logoutUser() {
            this.$emit('setUser', null);
            this.$router.push({ name: 'login' });
        },
        async deleteUser() {
            const isDelete = confirm(`Are you sure you want to delete ${this.user.username}?`);

            if (!isDelete) {
                return;
            }

            const response = await axios.delete(
                `http://localhost:3030/api/user/${this.user._id}`
            );

            console.log('User deleted successfully:', response.data);


            this.$emit('setUser', null);

            this.$router.push({ name: 'login' });
        },
        back() {
            this.$router.push({ name: 'home', params: { userId: this.user._id } });
        }
    }
}

</script>

