<template>
  <div class="main-header">
    <div class="logo">
      <img src="../assets/happy_leafy.png" alt="MongoDB Logo" />
      <h2>Payments Demo</h2>
    </div>
    <div class="nav">
      <div class="menu-btn">
        <i class="fas fa-bars fa-2x"></i>
      </div>
      <!-- <router-link to="{name : 'home', params : { userId: user._id}}" >Home</router-link> | -->
      <router-link :to="{ name: 'account', params: { id: user._id } }" v-if="user">My Accounts</router-link> |
      <router-link :to="{name : 'user' , query : {userId: user._id, username : user.username, email:user.email}}" v-if="user" >{{ user.username }}</router-link>
      <div v-if="user" class="notification-badge">
        <i class="fas fa-bell fa-2x"></i>
        <span @click=" showNotif = !showNotif " class="badge">{{ notifications.length }}</span>
        <div v-if="showNotif" class="notification">
          <div v-for="notif in notifications" :key="notif._id">
            <p>{{ new Date().toString("DD-MM-YYYY")}}  - {{notif.data }}</p>
          </div>
          <button @click="clearNotifications">Clear</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import socket from '../socket.js';

export default {
  name: 'MainHeader',
  props: {
    user: Object
  },
  data() {
    return {
      notifications: [],
      showNotif: false
    };
  },

  mounted() {
    this.setupSocket();
  },

  watch: {
    user(newUser, oldUser) {
      if (newUser?._id !== oldUser?._id) {
        this.setupSocket();
      }
    }
  },

  methods: {
    setupSocket() {
      if (this.user?._id) {
        console.log('Setting socket user:', this.user._id);
        socket.emit('set-user-socket', this.user._id);

        // Remove existing listener to avoid duplicates
        socket.off('add-notification', this.handleNotification);

        // Add new listener
        socket.on('add-notification', this.handleNotification);
      }
    },
    clearNotifications() {
      this.notifications = [];
      this.showNotif = false;
    },

    handleNotification(notification) {
      console.log('Received notification for current user:', notification);
      this.notifications.push(notification);
      // Process the notification as required
    }
  },



  beforeUnmount() {
    // Clean up the listener when the component is destroyed
    socket.off('add-notification', this.handleNotification);
  }
};

</script>

