import { createRouter, createWebHistory } from 'vue-router';
import MainHome from './components/Home.vue';
import LoginPage from './components/LoginPage.vue';
import MainAccount from './components/Account.vue';
import MainUser from './components/User.vue';
import MainTransaction from './components/Transaction.vue';

// Define routes
const routes = [
  {path: '/app/login',name : 'login', component: LoginPage},
  { path: '/app/home/:userId',name : 'home', component: MainHome, params : {userId: null}, props: true},
  { path: '/app/account/:id', component: MainAccount, name: 'account', params : {id: null}, props: true },
  {path:  '/app/user' , name : 'user', component: MainUser, query : {username : null, email : null}},
  { path: '/app/transaction/:id/:userId', component: MainTransaction, name: 'transaction' ,  params : {id: null, userId: null}, query: {trxId : null}, props: true},

];

// Create router instance
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
