<template>
    <form @submit.prevent="addAccount">
      <label for="accountType">Account Type</label>
      <select id="accountType" v-model="accountType">
        <option value="checking">Checking</option>
        <option value="savings">Savings</option>
     </select>
      <label for="accountBalance">Account Balance</label>
      <input type="number" id="accountBalance" v-model="accountBalance" />
      <div class="random-account">
      <label for="accountNumber">Account Number</label>
        <input type="text" id="accountNumber" v-model="accountNumber" />
        <button @click.prevent="generateRandomAccountNumber" >Generate Random Account Number</button>
    </div>
      <button type="submit">Add Account</button>
     </form>
</template>
<script>
import axios from 'axios';
export default {
  name: 'AddAccount',
  props: {
    user: Object
  },
    data() {
        return {
        accountType: 'checking',
        accountNumber: '',

        accountBalance: 1000
        }
    },
    methods:
    {
        generateRandomAccountNumber() {
            this.accountNumber = Math.floor(Math.random() * 1000000000);
        },
        async addAccount() {
            try {
                console.log('Adding account:', this.user);
                if (!this.accountNumber || !this.accountType || !this.accountBalance) {
                    alert('Please enter a valid account details');
                    return;
                }
                const response = await axios.post(
                    `http://localhost:3030/api/account`,
                    { userId: this.user._id,
                      "username" : this.user.username,
                      "accountDetails": {
                    "accountNumber": `${this.accountNumber}`, // Encrypted
                    "accountType": this.accountType,
                    "balance": this.accountBalance,
            "limitations": {
                 "withdrawalLimit": 100000,
                "transferLimit": 1000,
                "otherLimitations": "No Limit"

            },
            "securityTags": [],
            "details": {
                "IBAN" : `IL${this.accountNumber}`
            }// Encrypted string of sensitive data
             }
                    }
                );
                console.log('Account added successfully:', response.data);
                this.$emit('refreshUser' , response.data.user);
            } catch (error) {
                console.error('Account add failed:', error);
            }
        }
    }
  
}

</script>

<style scoped>
  form {
    display: flex;
    flex-direction: column;
    width: 300px;
  }
  label {
    margin-top: 10px;
  }
  input {
    margin-bottom: 10px;
  }
  button {
        margin-top: 10px;



  }
  </style>