<template>
      <div class="registration">
        <h2>User Login</h2>
        <input type="text" v-model="username" placeholder="User Name" />
        <input type="email" v-model="email" v-if="isRegister" placeholder="Email" />
        <input type="password" v-model="password" v-if="isRegister" placeholder="Password"  value="email@example.com"/>
        <input type="password" v-model="password" v-if="isRegister" placeholder="Confirm Password" />
        <button v-if="isRegister" @click="registerUser">Submit Registeration</button>
        <button v-if="!isRegister" @click="loginUser">Login</button>
        <button v-if="!isRegister" @click="isRegister = !isRegister">Register</button>
        <!-- <button @click="registerUser">Register</button> -->
      </div>
    </template>
    
    <script>
    import axios from 'axios';
    
    export default {
      emits: ['setUser'],
      data() {
        return {
          username: '',
            email: '',
            password: '',
            isRegister: false
        };
      },
      methods: {
        async loginUser() {
          try {
            if (!this.validateRegistration(this.username)) {
              alert('Please enter a valid username name');
              return;
            }
            const response = await axios.get(
              `http://localhost:3030/api/user/username/${this.username}`
            );
            console.log('Login successful:', response.data);
           
            console.log('Registration successful:', response.data);
            console.log('Navigating with userId:', response.data._id);
            this.$router.push({ name: 'home', params: { userId: response.data._id } });
            this.$emit('setUser', response.data );

          } catch (error) {
            alert("User not found, please register")
            console.error('Registration failed:', error);
          }
        },
        validateRegistration(username) {
          return username;
        },
        async registerUser() {
          try {
            if (!this.validateRegistration(this.username)) {
              alert('Please enter a valid username name');
              return;
            }
            const response = await axios.post(
              `http://localhost:3030/api/user/register`,
              { username: this.username , email: this.email, password: this.password}
            );
            this.$emit('setUser', response.data );
            this.$router.push({ name: 'home', params: { userId: response.data._id } });
            console.log('Registration successful:', response.data);
          } catch (error) {
            console.error('Registration failed:', error);
          }
        },
      },
       
    };
    
    </script>
    
