<template>
    <div class="main-transaction-search">
        <!-- Search transactions -->
        <div class="search-transaction">
            <h3>Search Transactions</h3>
            <div class="search-transaction-text">
                <input
                    v-model="searchQuery"
                    type="text"
                    placeholder="Search for transactions..."
                    class="search-input"
                />
                <button @click="fetchTransactions" class="search-button">Search</button>
            </div>
        </div>
        <div class="transaction-records">
            <h3>Transaction Records</h3>
            <div class="transaction-list">
                <div v-if="this.currentUser.transactions.length === 0">No transactions</div>
                <div v-for="trx in this.currentUser.transactions" :key="trx._id">
                    <details class="transaction-details">
                        
                        <summary>trxId : {{ trx._id }} | {{ trx.type === 'outgoing' ? '📤' : '📥' }} |<b>Amount</b> : {{ trx.amount  }}$ | <b>@</b> {{ new Date(trx.date)  }}</summary> 
                        <p>Type : {{ trx.type }}</p>
                        <p> From: {{trx.referenceData.sender.name}} </p>
                        <p> To: {{trx.referenceData.receiver.name}} </p>
                        <p> Details : {{ trx.details }}</p>
                        
                        <p>Amount: {{ trx.amount }}</p>
                        <p>Status: {{ trx.status }}</p>
                        <details>
                            <summary>Transaction steps</summary>
                            <p v-for="step in trx.steps" :key="step.api">
                               * {{ step.api }} - Completed: {{ step.completed }}</p>
                        </details>
                    </details>
                    <button v-if="trx.status === 'completed' && trx.type === 'outgoing'" @click="refund(trx._id)">Refund</button>
                    <hr><hr/>
                </div>
               
            </div>

        </div>
        <button @click="backToHome">Back</button>
    </div>

</template>

<script>
import axios from 'axios';

export default {
    name: 'TransactionSearch',
    props: ['id', 'userId'],
    emits: ['setUser'],
    mounted() {
        //console.log('UserId:', this.$route.query.userId);
        if (this.$route.query.trxId) {
            this.fetchTransactions();

        }
        else{
            this.refreshPage();
        }
        
    },
    data() {
        return {
            currentUser: {
                _id: this.id,
                userId: this.userId,
                transactions: []
            },
            searchQuery: this.$route.query.trxId  ? this.$route.query.trxId : ''
        }
    },
    methods: {
        async refreshPage() {
            try {
                const response = await axios.get(
                    `http://localhost:3030/api/transaction?accountId=${this.currentUser._id}`
                );
                console.log('User:', response.data);
                this.currentUser.transactions = response.data;
            } catch (error) {
                console.error('User not found:', error);
            }
        },
        refund(trxId) {
            const confirm = window.confirm(`Are you sure you want to refund ${trxId}?`);
            if (!confirm) {
                return;
            }
            const response = axios.put(
                `http://localhost:3030/api/transaction/refund/${trxId}`
            );
            console.log('Refund successful:', response.data);
        },
        backToHome() {
            this.$router.push({ name: 'home', params: { userId: this.currentUser.userId } });
        },
        async fetchTransactions() {
            try {
                const response = await axios.get(
                    `http://localhost:3030/api/transaction/${this.searchQuery}`
                );
                console.log('Transactions:', response.data);
                this.currentUser.transactions = [response.data];
            } catch (error) {
                console.error('Transactions not found:', error);
            }
        }
    }
}
</script>

<style scoped>
.main-transaction-search {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}

.search-transaction {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}
</style>